import { IBaseClient, IEvm, Options } from '../../types';
import { BaseClient } from '../../common/base-client';
import { Evm } from './evm';

export class EvmClient extends BaseClient implements IEvm {
  client: IBaseClient;

  send: IEvm['send'];

  contractExists: IEvm['contractExists'];

  call: IEvm['call'];

  contractConnect: IEvm['contractConnect'];

  constructor(options: Options) {
    super(options);

    const evm = new Evm(this, 'evm');

    this.send = evm.send;
    this.contractExists = evm.contractExists;
    this.call = evm.call;
    this.contractConnect = evm.contractConnect;
  }
}
